$(function() {
	var $upBtn = $('.upBtn')

	$(window).on('scroll', function(e) {
		showUpBtn(e);
		hideUpBtn(e);
	})
	function showUpBtn(e) {
		if (window.pageYOffset>100) {
			$upBtn.addClass('upBtn_show')
		}
	}

	function hideUpBtn(e) {
		if (window.pageYOffset<100) {
			$upBtn.removeClass('upBtn_show')
		}
	}

	$upBtn.on('click', function(e) {
		e.preventDefault();
		var elementToScroll = $(this.getAttribute('href')).offset().top;
		 $('html,body').animate({scrollTop:elementToScroll}, '1000');
	})
})